* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html, 
body,
#root {
	height: 100vh;
}

#root {
	width: 100%;
	display: flex;
}

body {
	height: 100vh;
	display: flex;
	background: #060AB2;
}

.container {
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.controls {
	margin-left: 20px;
}

.game {
	width: 640px;
	height: 100vh;
	margin: auto;
	display: flex;
	flex-wrap: wrap;
	perspective: 1000px
}

.card {
	width: calc(25% - 10px);
	height: calc(33.333% -10px);
	margin: 5px;
	position: relative;
	transform-style: preserve-3d;
	box-shadow: 1px 1px 1px rgba(0, 0, 0, .3);
	transform: scale(1);
	transform-style: preserve-3d;
	transition: transform .5s;
}

.card:active {
	transform: scale(0.97);
	transition: transform .2s;
}

.flip {
	transform: rotateY(180deg);
}

/*
  .finished {
	opacity: .5;
  }
  */

.front,
.back {
	width: 100%;
	height: 100%;
	padding: 20px;
	position: absolute;
	border-radius: 5px;
	background: #1C7CCC;
	backface-visibility: hidden;
}

.front {
	transform: rotateY(180deg);
}

.btn-restart {
	background:linear-gradient(to bottom, #77b55a 5%, #72b352 100%);
	background-color:#77b55a;
	border: none;
	border-radius:4px;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:18px;
	padding:10px 22px;
	text-decoration:none;
}
.btn-restart:hover {
	background:linear-gradient(to bottom, #72b352 5%, #77b55a 100%);
	background-color:#609944;
}
.btn-restart:active {
	position:relative;
	top:1px;
}

.win-text {
	color: #6cc0f8;
	margin-top: 20px;
	font-weight: 800;
	font-size: 25px;
}

.show-seconds {
	color: #6cc0f8;
	margin-top: 20px;
	font-weight: 800;
	font-size: 25px;
}

.levels-form {
	margin-bottom: 20px;
}

.levels-form label {
	display: block;
	color: #6cc0f8;
	font-weight: 800;
	font-size: 25px;
}

.levels-form select {
	display: block;
    width: 100%;
    padding: 0.375rem 2.25rem 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color: #fff;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
}